import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import { MyHttp } from "../../helper/myaxios";

export default function LogoutComponent() {
    var navigate = useNavigate();
    useEffect(() => {
        MyHttp.SetAccessToken(null);
        MyHttp.SetRefreshToken(null);
        
        Swal.fire("Thông báo", "Bạn đã đăng xuất, cảm ơn bạn đã sử dụng", "info");
        navigate('/login');
    });
    return (
        <p>111</p>
    );
}